import styled from 'styled-components'


const Column = styled.div`
	display: flex;
	flex-direction: column;
	padding: 15px;

	h1, h2, h3, h4 {
		font-family: "Inter";
	}
	li {
		font-family: 'Work Sans';
		padding: 5px;
		font-size: 19px;
	}
	p {
		font-size: 20px;
	}
`

const Heading = styled.h1`
	display: flex;
	justify-content: flex-start;
	font-size: 40px;
	font-family: "Inter";
	margin: 0;
	color: #FFC6AD;
	color: #2B3A67;
	padding: 10px;
	border-radius: 4px;
	display: inline;
	text-decoration: underline;
`

const Center = styled.div`
	display: flex;
	justify-content: center;
	h1 {
		font-size: 35px;
		padding-bottom: 5px;
		margin:0;
	}
	p {
		padding: 0;
		margin: 0;
		font-weight: bold;
	}
`

const Row = styled.div`
	display: flex;
	flex-direction: row;
	justify-content: ${props => props.center ? 'center' : 'space-around'};
	align-items: ${props => props.align ? 'center' : 'none'};
	@media (max-width: 768px) {
		flex-direction: column;
	}
	padding: 5px;
	margin-top: 10px;
`

const InlineSpan = styled.div`
	h2, p {
		display: inline;
	}
	p {
		margin-left: 8px;
	}
`

const StyledDiv = styled.div`
	background: #E8E7EE;
	border: 2px solid #2B3A67;
	padding: 20px;
	border-radius: 10px;
	margin-bottom: 10px;
`

const Card = styled.div`
	background: white;
	margin-right: 10px;
	padding: 0;
	width: 400px;
	h1 {
		text-align: center;
		color: #FFC6AD;
		background: #2B3A67;
		padding: 10px;
		margin-top: -3px;
		border-top-left-radius: 5px;
		border-top-right-radius: 5px;
		margin-right: -2px;
		margin-left: -2px;
	}
	li {
		padding: 3px;
		margin:0;
	}
	h2 {
		text-align: center;
	}
	border-radius: 5px;

	color: #2B3A67;
	background: #D1DBDB;
	border: solid;
	border-radius: #2B3A67;
`

const BoxHeading = styled.div`
	background: #2B3A67;
	color: #FFC6AD;
	padding: 15px 5px 15px 5px;
	margin-right: -21px;
	margin-left: -21px;
	margin-top: -21px;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
`


export {Column, Row, Heading, StyledDiv, InlineSpan, Card, Center, BoxHeading}